export class Character {
    _bitmaps;
    _animations;
    constructor() { }
    /**
     * @returns array of character animations.
     */
    get animations() {
        return [...this._animations];
    }
    /**
     * @returns array of character bitmaps.
     */
    get bitmaps() {
        return [...this._bitmaps];
    }
    /**
     * Creates new instance of Character from given data.
     *
     * @param bitmaps - list of character bitmaps
     * @param animations - list of character animations
     * @returns instance of Character class.
     */
    static fromData(bitmaps, animations) {
        const character = new Character();
        character._bitmaps = bitmaps;
        character._animations = animations;
        return character;
    }
}
