export class Texture {
    _u;
    _v;
    _maxU;
    _maxV;
    constructor() { }
    /**
     * Calculates u-axis value in atlas from local texture value.
     * @param u - u-axis value
     * @returns - value converted to atlas coordinates space.
     */
    convertUToAtlas(u) {
        return this._u + u * (this._maxU - this._u);
    }
    /**
     * Calculates v-axis value in atlas from local texture value.
     *
     * @param v - v-axis value
     * @returns - value converted to atlas coordinates space.
     */
    convertVToAtlas(v) {
        return this._v + v * (this._maxV - this._v);
    }
    /**
     * Creates new instance of Texture from given data.
     *
     * @param u - minimum u-axis value of texture in atlas
     * @param v - minimum v-axis value of texture in atlas
     * @param maxU - maximum u-axis value of texture in atlas
     * @param maxV - minimum v-axis value of texture in atlas
     * @returns instance of Texture class.
     */
    static fromData(u, v, maxU, maxV) {
        const texture = new Texture();
        texture._u = u;
        texture._v = v;
        texture._maxU = maxU;
        texture._maxV = maxV;
        return texture;
    }
}
