import { BitmapTransform } from "./bitmap-transform";
export class Bitmap {
    _mesh;
    _x;
    _y;
    _rotation;
    _scaleX;
    _scaleY;
    _texture;
    constructor() { }
    /**
     * @returns bitmap's mesh object.
     */
    get mesh() {
        return this._mesh;
    }
    /**
     * @returns bitmap's texture object.
     */
    get texture() {
        return this._texture;
    }
    /**
     * Calculates bitmap's transformation object.
     *
     * @returns instance of BitmapTransform class.
     */
    getTransform() {
        const tranform = new BitmapTransform();
        tranform.x = this._x;
        tranform.y = this._y;
        tranform.rotation = this._rotation;
        tranform.scaleX = this._scaleX;
        tranform.scaleY = this._scaleY;
        return tranform;
    }
    /**
     * Creates new instance of Bitmap from given data.
     *
     * @param texture - atlas texture object
     * @param mesh - mesh object
     * @param x - x-axis position
     * @param y - y-axis position
     * @param rotation - bitmap rotation
     * @param scaleX - x-axis scale
     * @param scaleY - y-axis scale
     * @returns instance of Bitmap class.
     */
    static fromData(texture, mesh, x, y, rotation, scaleX, scaleY) {
        const bitmap = new Bitmap();
        bitmap._texture = texture;
        bitmap._mesh = mesh;
        bitmap._x = x;
        bitmap._y = y;
        bitmap._rotation = rotation;
        bitmap._scaleX = scaleX;
        bitmap._scaleY = scaleY;
        return bitmap;
    }
}
