export class Mesh {
    _vertices;
    _indices;
    _texCoords;
    constructor() { }
    /**
     * @returns array of mesh vertices.
     */
    get vertices() {
        return [...this._vertices];
    }
    /**
     * @returns array of mesh indices.
     */
    get indices() {
        return [...this._indices];
    }
    /**
     * @returns array of mesh texture coordinates.
     */
    get texCoords() {
        return [...this._texCoords];
    }
    /**
     * Creates new instance of Mesh from given data.
     *
     * @param vertices - mesh vertices
     * @param indices - mesh indices
     * @param texCoords - mesh texture coordinates
     * @returns instance of Mesh class.
     */
    static fromData(vertices, indices, texCoords) {
        const mesh = new Mesh();
        mesh._vertices = vertices;
        mesh._indices = indices;
        mesh._texCoords = texCoords;
        return mesh;
    }
}
