import { BitmapTransform } from "./bitmap-transform";
import { TrackObjectType, TrackType } from "./track";
export class Animation {
    _name;
    _tracks;
    _length;
    /**
     * Calculates bitmaps' transforms for given frame of animation.
     *
     * @param frame - animation frame
     * @returns array of BitmapTransform objects.
     */
    getBitmapTransforms(frame) {
        const transforms = [];
        for (const track of this._tracks) {
            if (track.objectType === TrackObjectType.BITMAP) {
                const bitmapID = track.objectID;
                if (!transforms[bitmapID]) {
                    transforms[bitmapID] = new BitmapTransform();
                }
                const value = track.interpolate(frame);
                switch (track.type) {
                    case TrackType.POS_X:
                        transforms[bitmapID].x = value;
                        break;
                    case TrackType.POS_Y:
                        transforms[bitmapID].y = value;
                        break;
                    case TrackType.POS_ROT:
                        transforms[bitmapID].rotation = value;
                        break;
                    case TrackType.POS_SCALE_X:
                        transforms[bitmapID].scaleX = value;
                        break;
                    case TrackType.POS_SCALE_Y:
                        transforms[bitmapID].scaleY = value;
                        break;
                }
            }
        }
        return transforms;
    }
    /**
     * @returns name of animation.
     */
    get name() {
        return this._name;
    }
    /**
     * @returns length(number of frames) of animation.
     */
    get length() {
        return this._length;
    }
    /**
     * Creates new instance of Animation class from given data.
     *
     * @param name - animation name
     * @param length - animation lenght(number of frames)
     * @param tracks - array of animation tracks.
     * @returns instance of Animation class.
     */
    static fromData(name, length, tracks) {
        const animation = new Animation();
        animation._name = name;
        animation._length = length;
        animation._tracks = tracks;
        return animation;
    }
}
