import { mat4, vec3, vec4 } from "gl-matrix";
export class BitmapTransform {
    x;
    y;
    rotation;
    scaleX;
    scaleY;
    /**
     * Transforms array of vertices by internal BitmapTransform matrix.
     *
     * @param vertices - array of vertices to transform
     * @returns array of transformed vertices.
     */
    transformVertices(vertices) {
        const rot = mat4.create();
        mat4.identity(rot);
        mat4.rotateZ(rot, rot, -(((this.rotation || 0) * Math.PI) / 180.0));
        const translate = mat4.create();
        mat4.identity(translate);
        const vec = vec3.create();
        vec3.set(vec, this.x || 0, -this.y || 0, 0.0);
        mat4.translate(translate, translate, vec);
        const scale = mat4.create();
        mat4.identity(scale);
        const scaleVec = vec3.create();
        vec3.set(scaleVec, this.scaleX || 1, this.scaleY || 1, 1);
        mat4.scale(scale, scale, scaleVec);
        const finalMat = mat4.create();
        mat4.identity(finalMat);
        mat4.multiply(finalMat, scale, rot);
        mat4.multiply(finalMat, translate, finalMat);
        const transformVertices = [];
        for (let i = 0; i < vertices.length / 2; i++) {
            let transformVector = vec4.create();
            vec4.set(transformVector, vertices[2 * i], vertices[2 * i + 1], 0.0, 1.0);
            transformVector = vec4.transformMat4(transformVector, transformVector, finalMat);
            transformVertices.push(transformVector[0], transformVector[1]);
        }
        return transformVertices;
    }
}
